import React, { useEffect, useState } from "react";

const quotes = [
'Podrowa to y. Hans Christian Andersen',
'ycia nie mierzy si iloci oddechw, ale iloci chwil, ktre zapieraj dech w piersiach. Maya Angelou',
'Podrowanie to jedyna rzecz, ktr kupujesz i sprawia, e stajesz si bogatszy. Anonim',
'Podrujemy nie po to, by uciec przed yciem, ale by ycie nam nie ucieko. Anonim',
'Przygoda warta jest kadego trudu. Arystoteles',
'wiat jest jak ksika. Ci, ktrzy nie podruj, czytaj jedynie pierwsz stron. w. Augustyn',
'Poznaem mnstwo ludzi w Europie, odnalazem siebie. James Baldwin',
'Podrowanie sprawi, e zaniemwisz z wraenia, a potem zamienisz si w gawdziarza. Ibn Battuta',
'Prawdziwi podrnicy to ci tylko, ktrzy wyruszaj, aby wyruszy. Charles Baudelaire'

]

const InspiringQuote = (props) => {
  const [quote, setQuote] = useState('')

  useEffect(() => {
    setQuote(quotes[Math.floor(Math.random()*quotes.length)])
  }, [])
  return <p className="text-light text-center bg-dark p-1">{quote}</p>;
};

export default InspiringQuote;
